<template>
  <v-menu
    v-if="widget === 'inputDate'"
    :ref="field.name"
    v-model="menu[field.name]"
    :close-on-content-click="false"
    :return-value.sync="data[field.name]"
    :nudge-right="40"
    transition="scale-transition"
    offset-y
    min-width="auto"
  >
    <template v-slot:activator="{ on, attrs }">
      <v-text-field
        class="mt-0 pt-1"
        clearable
        :prepend-icon="mdi-calendar"
        v-model="fieldsData[field.name]"
        :id="field.id"
        :name="field.name"
        :placeholder="field.placeholder"
        :required="field.required"
        :disabled="field.disabled"
        readonly
        :class="field.css_classes"
        :messages="field.warning"
        :error-messages="field.error"
        v-bind="attrs"
        v-on="on"
      ></v-text-field>
    </template>
    <!-- only min -->
    <v-date-picker
      v-if="min.length > 0 && max.length === 0"
      scrollable
      @input="menu[field.name] = false"
      v-model="data[field.name]"
      year-icon="mdi-calendar-blank"
      color="primary"
      :min="min"
      :disabled="disabled"
      :readonly="readonly"
      :locale="locale"
    >
      <v-spacer></v-spacer>
      <v-btn text small color="primary" @click="menu[field.name] = false">{{ $t('message.18') }}</v-btn>
      <v-btn text small color="primary" @click="$refs[field.name][0].save(data[field.name])">OK</v-btn>
    </v-date-picker>
    <!-- only max -->
    <v-date-picker
      v-else-if="min.length === 0 && max.length > 0"
      scrollable
      @input="menu[field.name] = false"
      v-model="data[field.name]"
      year-icon="mdi-calendar-blank"
      color="primary"
      :max="max"
      :disabled="disabled"
      :readonly="readonly"
      :locale="locale"
    >
      <v-spacer></v-spacer>
      <v-btn text small color="primary" @click="menu[field.name] = false">{{ $t('message.18') }}</v-btn>
      <v-btn text small color="primary" @click="$refs[field.name][0].save(data[field.name])">OK</v-btn>
    </v-date-picker>
    <!-- min and max -->
    <v-date-picker
      v-else-if="min.length > 0 && max.length > 0"
      scrollable
      @input="menu[field.name] = false"
      v-model="data[field.name]"
      year-icon="mdi-calendar-blank"
      color="primary"
      :min="min"
      :max="max"
      :disabled="disabled"
      :readonly="readonly"
      :locale="locale"
    >
      <v-spacer></v-spacer>
      <v-btn text small color="primary" @click="menu[field.name] = false">{{ $t('message.18') }}</v-btn>
      <v-btn text small color="primary" @click="$refs[field.name][0].save(data[field.name])">OK</v-btn>
    </v-date-picker>
    <!-- without min and max -->
    <v-date-picker
      v-else
      scrollable
      v-model="data[field.name]"
      @input="menu[field.name] = false"
      year-icon="mdi-calendar-blank"
      color="primary"
      :disabled="disabled"
      :readonly="readonly"
      :locale="locale"
    >
      <v-spacer></v-spacer>
      <v-btn text small color="primary" @click="menu[field.name] = false">{{ $t('message.18') }}</v-btn>
      <v-btn text small color="primary" @click="$refs[field.name][0].save(data[field.name])">OK</v-btn>
    </v-date-picker>
  </v-menu>
</template>

<script>
export default {
  name: 'DatePicker',

  props: {
    data: {
      type: Object,
      default: function () {
        return {}
      }
    },
    disabled: {
      type: Boolean,
      default: false
    },
    readonly: {
      type: Boolean,
      default: false
    },
    min: {
      type: String,
      default: ''
    },
    max: {
      type: String,
      default: ''
    },
    locale: {
      type: String,
      default: 'en'
    },
    menu: {
      type: Object,
      default: function () {
        return {}
      }
    },
    field: {
      type: Object,
      default: function () {
        return {}
      }
    },
    widget: {
      type: String,
      default: ''
    }
  }
}
</script>
